import client from './client'

const getAllChatflows = () => client.get('/flows')

const getSpecificChatflow = (id) => client.get(`/flows/${id}`)

const getSpecificChatflowFromPublicEndpoint = (id) => client.get(`/public-chatflows/${id}`)

const createNewChatflow = (body) => client.post(`/flows`, body)
const createNewChatflowVersion = (id,body) => client.post(`/flows/${id}`, body)

const updateChatflow = (id, body) => client.put(`/flows/${id}`, body)

const deleteChatflow = (id) => client.delete(`/flows/${id}`)

const getIsChatflowStreaming = (id) => client.get(`/chatflows-streaming/${id}`)

export default {
    getAllChatflows,
    createNewChatflowVersion,
    getSpecificChatflow,
    getSpecificChatflowFromPublicEndpoint,
    createNewChatflow,
    updateChatflow,
    deleteChatflow,
    getIsChatflowStreaming
}
