import { useEffect, useRef, useState, useCallback, useContext } from 'react'
import ReactFlow, { addEdge, Controls, Background, useNodesState, useEdgesState } from 'reactflow'
import 'reactflow/dist/style.css'
import axios from 'axios'
import Accordion from '@mui/material/Accordion'
import AccordionSummary from '@mui/material/AccordionSummary'
import AccordionDetails from '@mui/material/AccordionDetails'
import Typography from '@mui/material/Typography'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore'
import { Sidebar, Menu, MenuItem, useProSidebar } from "react-pro-sidebar";
import HomeOutlinedIcon from "@mui/icons-material/HomeOutlined";
import InfoIcon from '@mui/icons-material/Info';

import { useDispatch, useSelector } from 'react-redux'
import { useNavigate, useLocation } from 'react-router-dom'
import { usePrompt } from '../../utils/usePrompt'
import {
    REMOVE_DIRTY,
    SET_DIRTY,
    SET_CHATFLOW,
    enqueueSnackbar as enqueueSnackbarAction,
    closeSnackbar as closeSnackbarAction
} from 'store/actions'
import { omit, cloneDeep } from 'lodash'

// material-ui
import { Toolbar, Box, AppBar, Button, TextField, Paper, Card } from '@mui/material'
import { useTheme } from '@mui/material/styles'

// project imports
import CanvasNode from './CanvasNode'
import ButtonEdge from './ButtonEdge'
import CanvasHeader from './CanvasHeader'
import AddNodes from './AddNodes'
import ConfirmDialog from 'ui-component/dialog/ConfirmDialog'
import { ChatPopUp } from 'views/chatmessage/ChatPopUp'
import { flowContext } from 'store/context/ReactFlowContext'

// API
import nodesApi from 'api/nodes'
import chatflowsApi from 'api/chatflows'

// Hooks
import useApi from 'hooks/useApi'
import useConfirm from 'hooks/useConfirm'

// icons
import { IconPlus, IconX } from '@tabler/icons'

// utils
import { getUniqueNodeId, initNode, getEdgeLabelName, rearrangeToolsOrdering } from 'utils/genericHelper'
import useNotifier from 'utils/useNotifier'

// const
import { FLOWISE_CREDENTIAL_ID } from 'store/constant'
import { ChatMessage } from 'views/chatmessage/ChatMessage'
import { StyledFab } from 'ui-component/button/StyledFab'
import { ExpandLess } from '@mui/icons-material'
import MainCard from 'ui-component/cards/MainCard'

const nodeTypes = { customNode: CanvasNode }
const edgeTypes = { buttonedge: ButtonEdge }

// ==============================|| CANVAS ||============================== //

const Canvas = () => {
    const theme = useTheme()
    const navigate = useNavigate()

    const { state } = useLocation()
    const templateFlowData = state?.flowDetails ? state.flowDetails : ''
    const URLpath = document.location.pathname.toString().split('/')
    console.log(URLpath)
    const chatflowId = URLpath[URLpath.length - 1] === 'canvas' ? '' : URLpath[URLpath.length - 1]

    const { confirm } = useConfirm()

    const dispatch = useDispatch()
    const canvas = useSelector((state) => state.canvas)
    const [canvasDataStore, setCanvasDataStore] = useState(canvas)
    const [addNode, setAddNode] = useState(true)
    const [chatflow, setChatflow] = useState(null)
    const [openInfoFlow, setOpenInfoFlow] = useState(false)

    const { reactFlowInstance, setReactFlowInstance } = useContext(flowContext)
    const [allNodeData, setAllNodeData] = useState([])

    const [chatOpen, setChatOpen] = useState(false)

    // ==============================|| Snackbar ||============================== //

    useNotifier()
    const enqueueSnackbar = (...args) => dispatch(enqueueSnackbarAction(...args))
    const closeSnackbar = (...args) => dispatch(closeSnackbarAction(...args))

    // ==============================|| ReactFlow ||============================== //

    const [nodes, setNodes, onNodesChange] = useNodesState()
    const [edges, setEdges, onEdgesChange] = useEdgesState()

    const [selectedNode, setSelectedNode] = useState(null)

    const reactFlowWrapper = useRef(null)

    // ==============================|| Chatflow API ||============================== //

    const getNodesApi = useApi(nodesApi.getAllNodes)
    const createNewChatflowApi = useApi(chatflowsApi.createNewChatflow)
    const createNewChatflowVersion = useApi(chatflowsApi.createNewChatflowVersion)
    const testChatflowApi = useApi(chatflowsApi.testChatflow)
    const updateChatflowApi = useApi(chatflowsApi.updateChatflow)
    const getSpecificChatflowApi = useApi(chatflowsApi.getSpecificChatflow)

    // ==============================|| Events & Actions ||============================== //

    const onConnect = (params) => {
        const handleType = params.sourceHandle.split('-')[2]
        const newEdge = {
            ...params,
            type: 'buttonedge',
            id: `${params.source}-${params.sourceHandle}-${params.target}-${params.targetHandle}`,
            data: { label: getEdgeLabelName(params.sourceHandle) },
            style:
                handleType === 'control'
                    ? {
                        stroke: 'red',
                        strokeWidth: 3
                    }
                    : {}
        }

        const targetNodeId = params.targetHandle.split('-')[0]
        const sourceNodeId = params.sourceHandle.split('-')[0]
        const targetInput = params.targetHandle.split('-')[2]

        setNodes((nds) =>
            nds.map((node) => {
                if (node.id === targetNodeId) {
                    setTimeout(() => setDirty(), 0)
                    let value
                    const inputAnchor = node.data.inputAnchors.find((ancr) => ancr.name === targetInput)
                    const inputParam = node.data.inputParams.find((param) => param.name === targetInput)

                    if (inputAnchor && inputAnchor.list) {
                        const newValues = node.data.inputs[targetInput] || []
                        if (targetInput === 'tools') {
                            rearrangeToolsOrdering(newValues, sourceNodeId)
                        } else {
                            newValues.push(`{{${sourceNodeId}.data.instance}}`)
                        }
                        value = newValues
                    } else if (inputParam && inputParam.acceptVariable) {
                        value = node.data.inputs[targetInput] || ''
                    } else {
                        value = `{{${sourceNodeId}.data.instance}}`
                    }
                    node.data = {
                        ...node.data,
                        inputs: {
                            ...node.data.inputs,
                            [targetInput]: value
                        }
                    }
                }
                return node
            })
        )

        setEdges((eds) => addEdge(newEdge, eds))
    }

    const handleLoadFlow = (file) => {
        try {
            // const flowData = JSON.parse(file)
            // const nodes = flowData.nodes || []
            // setNodes(nodes)
            // setEdges(flowData.edges || [])
            file?.flowData?.nodes?.forEach((node) => {
                let createNode = { id: node?.id, position: node.position, type: 'customNode', data: node?.data }
                setNodes((nds) => nds.concat(createNode))
            })

            file?.flowData?.edges?.forEach((edge) => {
                let createEdge = { id: edge.id, type: edge.type, source: edge.source, target: edge.target, sourceHandle: edge.sourceHandle, targetHandle: edge.targetHandle }
                setEdges((eds) => addEdge(createEdge, eds))
            })
            // setDirty()
        } catch (e) {
            console.error(e)
        }
    }

    const handleDeleteFlow = async () => {
        const confirmPayload = {
            title: `Delete`,
            description: `Delete chatflow ${chatflow.name}?`,
            confirmButtonName: 'Delete',
            cancelButtonName: 'Cancel'
        }
        const isConfirmed = await confirm(confirmPayload)

        if (isConfirmed) {
            try {
                await chatflowsApi.deleteChatflow(chatflow.id)
                navigate(-1)
            } catch (error) {
                const errorData = error.response.data || `${error.response.status}: ${error.response.statusText}`
                enqueueSnackbar({
                    message: errorData,
                    options: {
                        key: new Date().getTime() + Math.random(),
                        variant: 'error',
                        persist: true,
                        action: (key) => (
                            <Button style={{ color: 'white' }} onClick={() => closeSnackbar(key)}>
                                <IconX />
                            </Button>
                        )
                    }
                })
            }
        }
    }

    const handleSaveFlow = (chatflowName) => {
        console.log(chatflowName);
        if (reactFlowInstance) {
            const nodes = reactFlowInstance.getNodes().map((node) => {
                const nodeData = cloneDeep(node.data)
                if (Object.prototype.hasOwnProperty.call(nodeData.inputs, FLOWISE_CREDENTIAL_ID)) {
                    nodeData.credential = nodeData.inputs[FLOWISE_CREDENTIAL_ID]
                    nodeData.inputs = omit(nodeData.inputs, [FLOWISE_CREDENTIAL_ID])
                }
                node.data = {
                    ...nodeData,
                    selected: false
                }
                return node
            })

            const rfInstanceObject = reactFlowInstance.toObject()
            rfInstanceObject.nodes = nodes
            const flowData = rfInstanceObject

            if (!chatflow.id) {
                const newChatflowBody = {
                    displayName: chatflowName,
                    deployed: false,
                    isPublic: false,
                    flowData
                }
                createNewChatflowApi.request(newChatflowBody);

            } else {
                const updateBody = {
                    name: chatflowName,
                    flowData
                }
                updateChatflowApi.request(chatflow.id, updateBody)
            }
        }
    }

    // eslint-disable-next-line
    const onNodeClick = useCallback((event, clickedNode) => {
        setSelectedNode(clickedNode)
        setNodes((nds) =>
            nds.map((node) => {
                if (node.id === clickedNode.id) {
                    node.data = {
                        ...node.data,
                        selected: true
                    }
                } else {
                    node.data = {
                        ...node.data,
                        selected: false
                    }
                }

                return node
            })
        )
    })

    const onDragOver = useCallback((event) => {
        event.preventDefault()
        event.dataTransfer.dropEffect = 'move'
    }, [])

    const onDrop = useCallback(
        (event) => {
            event.preventDefault()
            const reactFlowBounds = reactFlowWrapper.current.getBoundingClientRect()
            let nodeData = event.dataTransfer.getData('application/reactflow')
            // check if the dropped element is valid
            if (typeof nodeData === 'undefined' || !nodeData) {
                return
            }

            nodeData = JSON.parse(nodeData)


            const position = reactFlowInstance.project({
                x: event.clientX - reactFlowBounds.left - 100,
                y: event.clientY - reactFlowBounds.top - 50
            })

            const newNodeId = getUniqueNodeId(nodeData, reactFlowInstance.getNodes())

            const newNode = {
                id: newNodeId,
                position,
                type: 'customNode',
                data: initNode(nodeData, newNodeId)
            }

            setSelectedNode(newNode)
            setNodes((nds) =>
                nds.concat(newNode).map((node) => {
                    if (node.id === newNode.id) {
                        node.data = {
                            ...node.data,
                            selected: true
                        }
                    } else {
                        node.data = {
                            ...node.data,
                            selected: false
                        }
                    }

                    return node
                })
            )
            setTimeout(() => setDirty(), 0)
        },

        // eslint-disable-next-line
        [reactFlowInstance]
    )

    const saveChatflowSuccess = () => {
        dispatch({ type: REMOVE_DIRTY })
        enqueueSnackbar({
            message: 'Chatflow saved',
            options: {
                key: new Date().getTime() + Math.random(),
                variant: 'success',
                action: (key) => (
                    <Button style={{ color: 'white' }} onClick={() => closeSnackbar(key)}>
                        <IconX />
                    </Button>
                )
            }
        })
    }

    const errorFailed = (message) => {
        enqueueSnackbar({
            message,
            options: {
                key: new Date().getTime() + Math.random(),
                variant: 'error',
                persist: true,
                action: (key) => (
                    <Button style={{ color: 'white' }} onClick={() => closeSnackbar(key)}>
                        <IconX />
                    </Button>
                )
            }
        })
    }

    const setDirty = () => {
        dispatch({ type: SET_DIRTY })
    }

    const openChatBox = () => {
        setChatOpen(!chatOpen)
    }

    const infoHandle = () => {
        setOpenInfoFlow(true)
    }

    const agentFlowHandle = () => {
        setOpenInfoFlow(false)
    }

    // ==============================|| useEffect ||============================== //

    // Get specific chatflow successful
    useEffect(() => {
        if (getSpecificChatflowApi.data) {
            const chatflow = getSpecificChatflowApi.data
            const initialFlow = chatflow.flowData ? JSON.parse(chatflow.flowData) : []
            setNodes(initialFlow.nodes || [])
            setEdges(initialFlow.edges || [])
            dispatch({ type: SET_CHATFLOW, chatflow })
        } else if (getSpecificChatflowApi.error) {
            const error = getSpecificChatflowApi.error
            const errorData = error.response.data || `${error.response.status}: ${error.response.statusText}`
            errorFailed(`Failed to retrieve chatflow: ${errorData}`)
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [getSpecificChatflowApi.data, getSpecificChatflowApi.error])

    // Create new chatflow successful
    useEffect(() => {
        if (createNewChatflowApi.data) {
            const chatflow = createNewChatflowApi.data
            dispatch({ type: SET_CHATFLOW, chatflow })
            saveChatflowSuccess()
            window.history.replaceState(null, null, `/canvas/${chatflow.id}`)
        } else if (createNewChatflowApi.error) {
            const error = createNewChatflowApi.error
            const errorData = error.response.data || `${error.response.status}: ${error.response.statusText}`
            errorFailed(`Failed to save chatflow: ${errorData}`)
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [createNewChatflowApi.data, createNewChatflowApi.error])

    // Update chatflow successful
    useEffect(() => {
        if (updateChatflowApi.data) {
            dispatch({ type: SET_CHATFLOW, chatflow: updateChatflowApi.data })
            saveChatflowSuccess()
        } else if (updateChatflowApi.error) {
            const error = updateChatflowApi.error
            const errorData = error.response.data || `${error.response.status}: ${error.response.statusText}`
            errorFailed(`Failed to save chatflow: ${errorData}`)
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [updateChatflowApi.data, updateChatflowApi.error])

    // Test chatflow failed
    useEffect(() => {
        if (testChatflowApi.error) {
            enqueueSnackbar({
                message: 'Test chatflow failed',
                options: {
                    key: new Date().getTime() + Math.random(),
                    variant: 'error',
                    persist: true,
                    action: (key) => (
                        <Button style={{ color: 'white' }} onClick={() => closeSnackbar(key)}>
                            <IconX />
                        </Button>
                    )
                }
            })
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [testChatflowApi.error])

    useEffect(() => setChatflow(canvasDataStore.chatflow), [canvasDataStore.chatflow])

    // Initialization
    useEffect(() => {
        if (chatflowId) {
            getSpecificChatflowApi.request(chatflowId)
        } else {
            if (localStorage.getItem('duplicatedFlowData')) {
                handleLoadFlow(localStorage.getItem('duplicatedFlowData'))
                setTimeout(() => localStorage.removeItem('duplicatedFlowData'), 0)
            } else {
                setNodes([])
                setEdges([])
            }
            dispatch({
                type: SET_CHATFLOW,
                chatflow: {
                    name: 'Untitled chatflow'
                }
            })
        }

        // getNodesApi.request()
        getNodesApi.request()

        // Clear dirty state before leaving and remove any ongoing test triggers and webhooks
        return () => {
            setTimeout(() => dispatch({ type: REMOVE_DIRTY }), 0)
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    console.log(getNodesApi)

    useEffect(() => {
        setCanvasDataStore(canvas)
    }, [canvas])

    useEffect(() => {
        function handlePaste(e) {
            const pasteData = e.clipboardData.getData('text')
            //TODO: prevent paste event when input focused, temporary fix: catch chatflow syntax
            if (pasteData.includes('{"nodes":[') && pasteData.includes('],"edges":[')) {
                handleLoadFlow(pasteData)
            }
        }

        window.addEventListener('paste', handlePaste)

        return () => {
            window.removeEventListener('paste', handlePaste)
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        if (templateFlowData) {
            handleLoadFlow(templateFlowData)
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [templateFlowData])

    usePrompt('You have unsaved changes! Do you want to navigate away?', canvasDataStore.isDirty)

    return (
        <>
            <Box>
                {/* <AppBar
                    enableColorOnDark
                    color='inherit'
                    elevation={1}
                    sx={{
                        top: '56px',
                        height: '54px',
                        bgcolor: theme.palette.background.default
                    }}
                > */}
                    <Toolbar  sx={{
                      
                      border: '1px solid lightgrey'
                    }} >
                        <CanvasHeader 
                            chatflow={chatflow}
                            handleSaveFlow={handleSaveFlow}
                            handleDeleteFlow={handleDeleteFlow}
                            handleLoadFlow={handleLoadFlow}
                            openChatBox={openChatBox}
                            templateFlowData={templateFlowData}
                            agentflowName={state?.agetWorkFlowValue}
                        />
                    </Toolbar>
                {/* </AppBar> */}
                {/* <Box sx={{
             
                    height: '100vh',
                    width: '5%',
                    position: 'absolute',
                    zIndex: '300',
                    border: '1px solid lightgrey'
                }}>
                    <Menu>
                        <MenuItem icon={<HomeOutlinedIcon />} onClick={agentFlowHandle}></MenuItem>
                        <MenuItem icon={<InfoIcon />} onClick={infoHandle}></MenuItem>
                    </Menu>
                </Box> */}
                {openInfoFlow && <Box className='box-inline' sx={{  height: '100vh', width: '100%',  position: 'fixed', left: '5%' }}>

                </Box>}
                {addNode && !openInfoFlow ? (
                    <Box
                        className='box-inline'
                        sx={{
                          
                            height: '100vh',
                            width: '20%',
                            zIndex: '300',
                            // left:
                            border: '1px solid lightgrey'
                        }}
                    >
                        <Paper sx={{ height: '90vh' }}>
                            <AddNodes nodesData={getNodesApi?.data} node={selectedNode} setAddNode={setAddNode} />
                        </Paper>
                    </Box>
                ) : !openInfoFlow && (
                    <Box
                        className='box-inline'
                        sx={{

                            position: 'fixed',

                            zIndex: '300'
                        }}
                    >
                        {' '}
                        <StyledFab
                            sx={{ left: 20, top: 8 }}
                            size='small'
                            aria-label='add'
                            title='Add Node'
                            variant='extended'
                            onClick={() => {
                                setAddNode(true)
                            }}
                        >
                            <IconPlus /> Add Nodes
                        </StyledFab>
                    </Box>
                )}
                {
                    !openInfoFlow && <Box className='box-inline' sx={{ height: '100vh', width: '100%', position: 'fixed' }}>
                        <div className='reactflow-parent-wrapper'>
                            <div className='reactflow-wrapper' ref={reactFlowWrapper}>
                                <ReactFlow
                                    nodes={nodes}
                                    edges={edges}
                                    onNodesChange={onNodesChange}
                                    onNodeClick={onNodeClick}
                                    onEdgesChange={onEdgesChange}
                                    onDrop={onDrop}
                                    onDragOver={onDragOver}
                                    onNodeDragStop={setDirty}
                                    nodeTypes={nodeTypes}
                                    edgeTypes={edgeTypes}
                                    onConnect={onConnect}
                                    onInit={setReactFlowInstance}
                                    fitView
                                    deleteKeyCode={canvas.canvasDialogShow ? null : ['Backspace', 'Delete']}
                                    minZoom={0.1}
                                    maxZoom={1.2}
                                >
                                    <Controls
                                    // position='top-center'
                                    // style={{
                                    //     display: 'flex',
                                    //     flexDirection: 'row',
                                    //     left: '50%',
                                    //     transform: 'translate(-50%, -50%)'
                                    // }}
                                    />
                                    <Background color='#aaa' gap={16} />
                                    <div className='node-ai-chat'>
                                        <Accordion>
                                            <AccordionSummary
                                                sx={{ backgroundColor: 'gainsboro' }}
                                                expandIcon={<ExpandLess />}
                                                aria-controls='panel1a-content'
                                                id='panel1a-header'
                                            >
                                                <Typography>Update Flow Through AI</Typography>
                                            </AccordionSummary>
                                            <AccordionDetails sx={{ border: '1px solid lightgray', padding: '0px', position: "relative", top: "-283px" }}>
                                                <Paper>
                                                    <ChatMessage chatType='nodeai' />
                                                </Paper>
                                            </AccordionDetails>
                                        </Accordion>
                                    </div>
                                    {/* <ChatPopUp chatflowid={chatflowId} /> */}
                                </ReactFlow>
                            </div>
                        </div>
                    </Box>
                }
                {chatOpen && (
                    <Box
                        className='box-inline'
                        sx={{

                            height: '100vh',
                            width: '25%',
                            position: 'absolute',

                            border: '1px solid lightgrey'
                        }}
                    >
                        <Paper>
                            <ChatMessage chatType='testchat' />
                        </Paper>
                    </Box>
                )}
                <ConfirmDialog />
            </Box >
        </>
    )
}

export default Canvas
